import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from 'chart.js';

import { v4 as uuid } from 'uuid';
import { Radar } from 'react-chartjs-2';
import { Container, Row, Col } from 'react-bootstrap';
import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import PropTypes from 'prop-types';
import Fade from 'react-reveal';
import Header from './Header';
import endpoints from '../constants/endpoints';
import FallbackSpinner from './FallbackSpinner';

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
);

ChartJS.defaults.backgroundColor = 'rgb(100, 153, 233, 0.2)';
ChartJS.defaults.borderColor = 'rgb(100, 153, 233, 1)';
ChartJS.defaults.color = '#000';
ChartJS.defaults.borderWidth = 0.25;

const styles = {
  iconStyle: {
    height: 75,
    width: 75,
    margin: 10,
    marginBottom: 0,
  },
  introTextContainer: {
    whiteSpace: 'pre-wrap',
    fontSize: '1.2em',
    margin: 20,
    textAlign: 'left',
  },
};

function showChart(skill) {
  const newData = {
    labels: skill.items.map((item) => item.label),
    datasets: [
      {
        label: skill.title,
        data: skill.items.map((item) => item.value),
      },
    ],
  }; return (
    <Col xl={4} lg={4} sm={6} xs={12} key={uuid()}>
      <Radar data={newData} width={370} height={370} options={{ maintainAspectRatio: false }} />
    </Col>
  );
}

function Skills(props) {
  const { header } = props;

  const [data, setData] = useState(null);

  const renderSkillsIntro = (intro) => (
    <h4 style={styles.introTextContainer}>
      <ReactMarkdown children={intro} />
    </h4>
  );

  useEffect(() => {
    fetch(endpoints.skills, {
      method: 'GET',
    })
      .then((res) => res.json())
      .then((res) => setData(res))
      .catch((err) => err);
  }, []);

  return (
    <>
      <Header title={header} />
      {data ? (
        <Fade>
          <div className="section-content-container">
            <Container>
              {renderSkillsIntro(data.intro)}
              <br />
              <br />
              <Row style={{ margin: '20' }}>
                {data.skills.map((skill) => showChart(skill))}
              </Row>
            </Container>
          </div>
        </Fade>
      ) : <FallbackSpinner /> }
    </>
  );
}

Skills.propTypes = {
  header: PropTypes.string.isRequired,
};

export default Skills;
